<template>
    <div class="header-screen__left-box screen-lang"
        @click.prevent="showLangMenu=!showLangMenu;$parent.showServicesMenu=false"
        :class="{'show': showLangMenu, 'mod-x': $store.getters['auth/isLoggedIn'](['customer'])}">
        <!-- <img class="icon-globe" src="~@/assets/img/icon/globe-icon.svg" alt="lang"> -->
        <!-- <img class="header-screen__arrow major-screen-arrow" src="~@/assets/img/icon/modal-arrow-down.svg" alt="Arrow Down"> -->
        <svg class="icon-globe" id="baseline-language-24px" xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
            <path id="Path_330" data-name="Path 330" d="M0,0H24V24H0Z" fill="none" />
            <path fill="currentColor" id="Path_331" data-name="Path 331"
                d="M11.99,2A10,10,0,1,0,22,12,10,10,0,0,0,11.99,2Zm6.93,6H15.97a15.649,15.649,0,0,0-1.38-3.56A8.03,8.03,0,0,1,18.92,8ZM12,4.04A14.087,14.087,0,0,1,13.91,8H10.09A14.087,14.087,0,0,1,12,4.04ZM4.26,14a7.822,7.822,0,0,1,0-4H7.64a16.515,16.515,0,0,0-.14,2,16.515,16.515,0,0,0,.14,2Zm.82,2H8.03a15.649,15.649,0,0,0,1.38,3.56A7.987,7.987,0,0,1,5.08,16ZM8.03,8H5.08A7.987,7.987,0,0,1,9.41,4.44,15.649,15.649,0,0,0,8.03,8ZM12,19.96A14.087,14.087,0,0,1,10.09,16h3.82A14.087,14.087,0,0,1,12,19.96ZM14.34,14H9.66a14.713,14.713,0,0,1-.16-2,14.585,14.585,0,0,1,.16-2h4.68a14.585,14.585,0,0,1,.16,2A14.713,14.713,0,0,1,14.34,14Zm.25,5.56A15.649,15.649,0,0,0,15.97,16h2.95A8.03,8.03,0,0,1,14.59,19.56ZM16.36,14a16.515,16.515,0,0,0,.14-2,16.515,16.515,0,0,0-.14-2h3.38a7.822,7.822,0,0,1,0,4Z" />
        </svg>



        <svg class="major-screen-arrow" id="baseline-keyboard_arrow_down-24px" xmlns="http://www.w3.org/2000/svg"
            width="24" height="24" viewBox="0 0 24 24">
            <path id="Path_1411" data-name="Path 1411" d="M7.41,8.59,12,13.17l4.59-4.58L18,10l-6,6L6,10Z"
                fill="currentColor" />
            <path id="Path_1412" data-name="Path 1412" d="M0,0H24V24H0Z" fill="none" />
        </svg>



        <div class="major-screen-dropdown">
            <a href="#" class="major-screen-dropdown__item" v-for="language in languages" :key="language.code"
                @click.prevent="select(language)">
                {{ language.label }}
            </a>
        </div>
    </div>
</template>

<script>
    import axios from "axios";

    export default {
        name: "Languages",
        data() {
            return {
                showLangMenu: false,
                selectedLanguage: {},
                languages: [{
                        code: 'he',
                        label: 'Hebrew',
                        shortLabel: 'Heb',
                    },
                    {
                        code: 'en',
                        label: 'English',
                        shortLabel: 'Eng',
                    },
                ],
            }
        },
        methods: {
            select(language) {
                this.selectedLanguage = language;
                this.$ml.change(language.code)

                axios.defaults.headers['Accept-Language'] = language.code;
                // axios.defaults.headers.common['Accept-Language'] = language.code;

                document.documentElement.setAttribute('lang', language.code);
                document.documentElement.setAttribute('dir', language.code == 'he' ? 'rtl' : 'ltr');
            },
        },
        mounted() {
            this.selectedLanguage = this.languages.find(language => language.code === this.$ml.current);
        }
    }
</script>

