<template>
    <!-- need-help-form -->
    <section class="need-help-form" id="contacts">
        <div class="container">
            <form id="help-form" class="help-form">
                <!-- help-form__right -->
                <div class="help-form__right help-form__right--order">
                    <h3 class="help-form__header third-header">{{ $ml.get('helpForm.title') }}</h3>
                    <p class="help-form__text second-text">{{ $ml.get('helpForm.desc') }}</p>
                    <div class="help-form__available">{{ $ml.get('helpForm.links') }}</div>
                    <!-- help-form__link-wrapper -->
                    <div class="help-form__link-wrapper">
                        <div class="help-form__link-box help-form__link-box-4">
                            <a class="help-form__link" :href="$ml.get('contacts.whatsapp.url')">
                                <img src="~@/assets/img/icon/help-form-whatsapp.svg" alt="Whatsapp">
                            </a>
                            <span>{{ $ml.get('contacts.whatsapp.text') }}</span>
                        </div>
                        <!--                        <div class="help-form__link-box help-form__link-box-3">-->
                        <!--                            <a class="help-form__link" :href="$ml.get('contacts.camera.url')">-->
                        <!--                                <img src="~@/assets/img/icon/help-center-camera.svg" alt="Camera">-->
                        <!--                            </a>-->
                        <!--                            <span>{{ $ml.get('contacts.camera.text') }}</span>-->
                        <!--                        </div>-->
                        <div class="help-form__link-box help-form__link-box-2">
                            <a class="help-form__link" :href="$ml.get('contacts.phone.url')">
                                <img src="~@/assets/img/icon/help-form-phone.svg" alt="Phone">
                            </a>
                            <span>{{ $ml.get('contacts.phone.text') }}</span>
                        </div>
                        <div class="help-form__link-box help-form__link-box-1">
                            <a class="help-form__link" :href="$ml.get('contacts.email.url')">
                                <img src="~@/assets/img/icon/help-form-envelope.svg" alt="Envelope">
                            </a>
                            <span>{{ $ml.get('contacts.email.text') }}</span>
                        </div>
                    </div>
                </div>

                <!-- help-form__left -->
                <div class="help-form__left">
                    <div class="help-form__title">{{ $ml.get('helpForm.form.title') }}</div>

                    <div class="help-form__group" :class="{invalid: errors.name || validation.hasError('helpFormData.name')}">
                        <input class="help-form__input-name help-form__input" type="text"
                               :placeholder="$ml.get('helpForm.form.name')" v-model="helpFormData.name">
                        <span class="help-form__error">{{ errors.name || validation.firstError('helpFormData.name') }}</span>
                    </div>
                    <div class="help-form__group" :class="{invalid: errors.email || validation.hasError('helpFormData.email')}">
                        <input class="help-form__input-name help-form__input" type="email"
                               :placeholder="$ml.get('helpForm.form.email')" v-model="helpFormData.email">
                        <span class="help-form__error">{{ errors.email || validation.firstError('helpFormData.email') }}</span>
                    </div>
                    <div class="help-form__group" :class="{invalid: errors.content || validation.hasError('helpFormData.content')}">
                    <textarea class="help-form__textarea help-form__input" :placeholder="$ml.get('helpForm.form.msg')"
                              v-model="helpFormData.content"></textarea>
                        <span class="help-form__error">{{ errors.content || validation.firstError('helpFormData.content') }}</span>
                    </div>
                    <div class="help-form__group"  :class="{invalid: errors.agree || validation.hasError('helpFormData.agree')}">
                        <div class="help-form__checkbox-wrapper">
                            <input class="help-form__input-checkbox" type="checkbox" id="inputCheckbox"
                                   v-model="helpFormData.agree">
                            <label for="inputCheckbox" class="help-form__label">
                                {{ $ml.get('helpForm.form.agree') }}
                            </label>
                        </div>
                        <span class="help-form__error">{{ errors.agree || validation.firstError('helpFormData.agree')  }}</span>
                    </div>

                    <button @click.prevent="send()" type="submit"
                            class="help-form__btn default-second-btn">{{ $ml.get('helpForm.form.button') }}
                    </button>
                </div>
            </form>

        </div>
        <div class="need-help-form__decoration"></div>
    </section>
</template>

<script>
    import Vue from 'vue';

    import {mapGetters} from 'vuex';
    import SimpleVueValidation from 'simple-vue-validator';
    const Validator = SimpleVueValidation.Validator;

    Vue.use(SimpleVueValidation);

    export default {
        name: "HelpForm",
        data() {
            return {
                helpFormData: {
                    name: '',
                    email: '',
                    content: '',
                    agree: false,
                }
            }
        },
        validators: {
            'helpFormData.name': function (value) {
                // &^%$#@
                return Validator.value(value).required().minLength(1).maxLength(254).regex('^(\\w+?|\\d+?|\\s+?|[`\'\"()-_=+*!?\u0590-\u05FF]+?)+$');
            },
            'helpFormData.content': function (value) {
                return Validator.value(value).required().minLength(25).maxLength(2000).regex('^(\\w+?|\\d+?|\\s+?|[`\'\"()-_=+*!?\u0590-\u05FF]+?)+$');
            },
            'helpFormData.agree': function (value) {
                return Validator.custom(function () {
                    if(!value) {
                        if(localStorage.getItem('vueml-lang') == 'en') {
                            return 'Please tick the checkbox.'
                        } else {
                            return 'פורמט לא חוקי.';
                        }
                    }
                });
            },
            'helpFormData.email': function (value) {
                return Validator.value(value).required().email();
            }
        },
        mounted() {
            this.$store.commit('help/setErrors');

            this.changeErrorsLang();
            let bus = this.$ml.bus;
            let _this = this;

            bus.$on('vueml-language-changed', function (newLanguage) {
                _this.changeErrorsLang();

                if(_this.helpFormIsLoading == 'validate') {
                    _this.send();
                }
            });
        },
        computed: {
            ...mapGetters({
                helpFormIsLoading: 'help/helpFormIsLoading',
                errors: 'help/getErrors',
            }),
        },
        methods: {
            send() {
                let $this = this;
                this.$store.commit('help/setFormIsLoading', 'validate');

                this.$validate()
                    .then(async function (success) {
                        if (success) {
                            if ($this.helpFormIsLoading !== 'loading') {
                                await $this.$store.dispatch('help/sendFeedback', $this.helpFormData);

                                if ($this.helpFormIsLoading == 'success') {
                                    alert($this.$ml.get('helpForm.alert'));
                                    $this.reset();
                                }
                            }
                        }
                    });
            },
            reset: function() {
                this.helpFormData = {
                    name: '',
                    email: '',
                    content: '',
                    agree: false,
                };
                this.validation.reset();
            },
            changeErrorsLang() {
                if(this.$ml.current == 'en') {
                    SimpleVueValidation.extendTemplates({
                        check: 'This field must be check.',
                        error: 'Error.',
                        required: 'This field is required.',
                        float: 'Must be a number.',
                        integer: 'Must be an integer.',
                        number: 'Must be a number.',
                        lessThan: 'Must be less than {0}.',
                        lessThanOrEqualTo: 'Must be less than or equal to {0}.',
                        greaterThan: 'Must be greater than {0}.',
                        greaterThanOrEqualTo: 'Must greater than or equal to {0}.',
                        between: 'Must be between {0} and {1}.',
                        size: 'Size must be {0}.',
                        length: 'Length must be {0}.',
                        minLength: 'Must have at least {0} characters.',
                        maxLength: 'Must have up to {0} characters.',
                        lengthBetween: 'Length must between {0} and {1}.',
                        in: 'Must be {0}.',
                        notIn: 'Must not be {0}.',
                        match: 'Not matched.',
                        regex: 'Invalid format.',
                        digit: 'Must be a digit.',
                        email: 'Invalid email.',
                        url: 'Invalid url.',
                        optionCombiner: function (options) {
                            if (options.length > 2) {
                                options = [options.slice(0, options.length - 1).join(', '), options[options.length - 1]];
                            }
                            return options.join(' or ');
                        }
                    });
                }
                else {
                    SimpleVueValidation.extendTemplates({
                        check: 'שדה זה חייב להיות סימון.',
                        error: 'Error.',
                        required: 'זהו שדה חובה.',
                        float: 'Must be a number.',
                        integer: 'Must be an integer.',
                        number: 'Must be a number.',
                        lessThan: 'חייב להיות פחות מ {0}.',
                        lessThanOrEqualTo: 'חייב להיות פחות או שווה ל- {0}.',
                        greaterThan: 'Must be greater than {0}.',
                        greaterThanOrEqualTo: 'Must greater than or equal to {0}.',
                        between: 'Must be between {0} and {1}.',
                        size: 'Size must be {0}.',
                        length: 'אורך חייב להיות {0}.',
                        minLength: 'חייבת לפחות {0} דמויות.',
                        maxLength: 'חייב להיות עד {0} דמויות.',
                        lengthBetween: 'אורך חייב בין {0} ו {1}.',
                        in: 'Must be {0}.',
                        notIn: 'Must not be {0}.',
                        match: 'Not matched.',
                        regex: 'פורמט לא חוקי.',
                        digit: 'Must be a digit.',
                        email: 'אימייל שגוי.',
                        url: 'Invalid url.',
                        optionCombiner: function (options) {
                            if (options.length > 2) {
                                options = [options.slice(0, options.length - 1).join(', '), options[options.length - 1]];
                            }
                            return options.join(' or ');
                        }
                    });
                }
            }
        },
        created() {
        },
    }
</script>

<style scoped>

</style>