<template>
  <!-- при скроле добавляем класс .scroll -->
  <header id="header-screen" class="header-screen major-screen__header" :class="{'scroll': stickyHeader}">

    <div class="header-screen__container">
      <!-- <img class="header-screen-hamburger" src="~@/assets/img/icon/hamburger.svg" alt=""
        @click.prevent="showHamburger = true"> -->

      <svg @click.prevent="showHamburger = true" class="header-screen-hamburger"  xmlns="http://www.w3.org/2000/svg" width="30" height="11" viewBox="0 0 30 11">
        <line id="Line_77" data-name="Line 77" x2="30" transform="translate(0 1)" fill="none" stroke="currentColor"
          stroke-width="2" />
        <line id="Line_78" data-name="Line 78" x2="30" transform="translate(0 10)" fill="none" stroke="currentColor"
          stroke-width="2" />
      </svg>


      <div class="header-screen__right" v-click-outside="vcoConfig">

        <div class="header-screen__dropdown major-screen-about">
          <router-link :to="{ name: 'about' }" class="header-screen__dropdown-text">{{
            $ml.get('header.about') }}</router-link>
        </div>
        <div class="header-screen__dropdown major-screen-services"
          @click.prevent="showServicesMenu=!showServicesMenu;$refs.languages.showLangMenu=false"
          :class="{'show': showServicesMenu}">
          <a href="#" class="header-screen__dropdown-text">{{ $ml.get('header.services') }}</a>
          <!-- <img class="major-screen-arrow" src="~@/assets/img/icon/modal-arrow-down.svg" alt="Arrow Down"> -->

          <svg class="major-screen-arrow" id="baseline-keyboard_arrow_down-24px" xmlns="http://www.w3.org/2000/svg"
            width="24" height="24" viewBox="0 0 24 24">
            <path id="Path_1411" data-name="Path 1411" d="M7.41,8.59,12,13.17l4.59-4.58L18,10l-6,6L6,10Z"
              fill="currentColor" />
            <path id="Path_1412" data-name="Path 1412" d="M0,0H24V24H0Z" fill="none" />
          </svg>



          <div class="major-screen-dropdown major-screen-services__dropdown">
            <a class="major-screen-dropdown__item" href="#"
              @click.prevent="openUrl('http://mixel.co.il/clients/eternity/')">{{ $ml.get('header.servicesLink.link1') }}</a>
            <a class="major-screen-dropdown__item" href="#"
              @click.prevent="openUrl('http://mixel.co.il/clients/eternity/obituaries')">{{ $ml.get('header.servicesLink.link2') }}</a>
            <a class="major-screen-dropdown__item" href="#"
              @click.prevent="openUrl('http://mixel.co.il/clients/eternity/gravestones')">{{ $ml.get('header.servicesLink.link3') }}</a>
          </div>
        </div>
      </div>

      <div class="header-screen__logo" :class="{'mod-x': $store.getters['auth/isLoggedIn'](['customer'])}">
        <router-link :to="{ name: 'home' }">
          <!-- <img src="~@/assets/img/major-logo.svg" alt="Logo">
           -->
          <svg class="header-screen__logo-img" xmlns="http://www.w3.org/2000/svg" width="67.845" height="33.523"
            viewBox="0 0 67.845 33.523">
            <path id="Path_6526" data-name="Path 6526"
              d="M353.689,352.756c16.68,0,27.747-6.372,27.747-10.586s-11.067-10.586-27.747-10.586-27.747,6.372-27.747,10.586,11.067,10.586,27.747,10.586m0,6.175c-18.735,0-33.922-7.5-33.922-16.762s15.187-16.762,33.922-16.762,33.923,7.5,33.923,16.762S372.424,358.931,353.689,358.931Z"
              transform="translate(-319.767 -325.408)" fill="currentColor" />
          </svg>
          <span>Eternity</span>
        </router-link>
      </div>

      <div class="header-screen__left" v-click-outside="vcoConfig">

        <a href="tel:+97237381800" class="header-screen__phone">
          <!-- <img src="~@/assets/img/icon/head-tel.svg" alt="">
           -->
          <svg class="header-screen__phone-img" xmlns="http://www.w3.org/2000/svg" width="24" height="24"
            viewBox="0 0 24 24">
            <path fill="none" d="M0 0h24v24H0z" />
            <path fill="currentColor"
              d="M17.38 10.79l-2.2-2.2a1 1 0 0 1-.25-1.02A11.36 11.36 0 0 0 15.5 4a1 1 0 0 1 1-1H20a1 1 0 0 1 1 1A17 17 0 0 1 4 21a1 1 0 0 1-1-1v-3.49a1 1 0 0 1 1-1 11.407 11.407 0 0 0 3.57-.57.994.994 0 0 1 1.02.24l2.2 2.2a15.149 15.149 0 0 0 6.59-6.59z" />
          </svg>

          <span>
            97-237381800
          </span>
        </a>

        <Languages ref="languages" />

        <a v-if="!$store.getters['auth/isLoggedIn'](['customer'])" @click.prevent="login()" href="#"
          class="header-screen__button">{{ $ml.get('header.login') }}</a>
        <!--            <a v-else @click.prevent="$store.dispatch('auth/logout')" href="#" class="header-screen__button">{{ $ml.get('header.logout') }}</a>-->
        <!--            $store.dispatch('auth/login')-->


      </div>

      <transition name="fade">
        <div class="menu" v-if="showHamburger">
          <div class="menu__top">
            <img class="menu__humburger" src="~@/assets/img/icon/humburger-close.svg"
              @click.prevent="showHamburger = false">
            <div class="menu__logo-box">
              <img src="~@/assets/img/icon/menu-logo.svg" alt="Logo">
            </div>
            <div class="menu__top-box">
              <languages />
              <button v-if="!$store.getters['auth/isLoggedIn'](['customer'])" @click.prevent="login()" type="button"
                class="menu__btn">{{ $ml.get('menu.login') }}
              </button>
              <!--                <button v-else @click.prevent="$store.dispatch('auth/logout')" type="button" class="menu__btn">-->
              <!--                    {{ $ml.get('header.logout') }}-->
              <!--                </button>-->
            </div>
          </div>

          <div class="menu__middle">
            <button @click.prevent="openUrl('http://mixel.co.il/clients/eternity/')" type="button"
              class="menu__button default-btn">{{ $ml.get('menu.button1') }}<span>{{ $ml.get('menu.button1Span') }}</span>
            </button>
            <button @click.prevent="openUrl('http://mixel.co.il/clients/eternity/obituaries')" type="button"
              class="menu__button default-btn">{{ $ml.get('menu.button2') }}
            </button>
            <button @click.prevent="openUrl('http://mixel.co.il/clients/eternity/gravestones')" type="button"
              class="menu__button default-btn menu__button--mod">{{ $ml.get('menu.button3') }}
            </button>

            <router-link :to="{ name: 'about' }" class="menu__link">{{
              $ml.get('menu.link1') }}
            </router-link>
            <!--                    <a href="#" class="menu__link">{{ $ml.get('menu.link2') }}</a>-->
            <a href="#" class="menu__link"
              @click.prevent="showHamburger=false;$parent.$refs.helpCenter.showHelpCenter=true">{{ $ml.get('menu.link3') }}</a>
            <a href="#contacts" class="menu__link" @click="showHamburger=false" v-smooth-scroll>{{
              $ml.get('menu.link4') }}</a>
          </div>

          <div class="menu__bottom">
            <div class="menu__title">{{ $ml.get('menu.contacts') }}</div>
            <div class="menu__conection">
              <div class="menu__conection-item">
                <a class="menu__relink" :href="$ml.get('contacts.whatsapp.url')">
                  <img src="~@/assets/img/icon/menu-whatsap.svg" alt="">
                </a>
                <span>{{ $ml.get('contacts.whatsapp.text') }}</span>
              </div>
              <!--                        <div class="menu__conection-item">-->
              <!--                            <a class="menu__relink" :href="$ml.get('contacts.camera.text')">-->
              <!--                                <img src="~@/assets/img/icon/menu-camera.svg" alt="">-->
              <!--                            </a>-->
              <!--                            <span>{{ $ml.get('contacts.camera.text') }}</span>-->

              <!--                        </div>-->
              <div class="menu__conection-item">
                <a class="menu__relink" :href="$ml.get('contacts.phone.url')">
                  <img src="~@/assets/img/icon/menu-phone.svg" alt="">
                </a>
                <span>{{ $ml.get('contacts.phone.text') }}</span>

              </div>
              <div class="menu__conection-item">
                <a class="menu__relink" :href="$ml.get('contacts.email.url')">
                  <img src="~@/assets/img/icon/menu-envelope.svg" alt="">
                </a>
                <span>{{ $ml.get('contacts.email.text') }}</span>
              </div>
            </div>
          </div>
        </div>
      </transition>
    </div>
  </header>
</template>

<script>
  import Languages from "@/components/Languages";

  export default {
    name: 'Header',
    components: {
      Languages
    },
    props: {},
    data() {
      return {
        stickyHeader: false,
        showHamburger: false,
        showServicesMenu: false,
        vcoConfig: {
          handler: this.onClickOutside,
          middleware: this.middleware,
          events: ['dblclick', 'click'],
          // Note: The default value is true, but in case you want to activate / deactivate
          //       this directive dynamically use this attribute.
          isActive: true,
          // Note: The default value is true. See "Detecting Iframe Clicks" section
          //       to understand why this behaviour is behind a flag.
          detectIFrame: true
        }
      }
    },
    methods: {
      // Note: The middleware will be executed if the event was fired outside the element.
      //       It should have only sync functionality and it should return a boolean to
      //       define if the handler should be fire or not
      middleware(event) {
        // console.log(event.target.className);
        return event.target.className.indexOf('header-screen__arrow') == -1 &&
          event.target.className.indexOf('header-screen__dropdown-text') == -1 &&
          event.target.className.indexOf('major-screen-dropdown__item') == -1 &&
          event.target.className.indexOf('icon-globe') == -1 &&
          event.target.className.indexOf('major-screen-arrow') == -1 &&
          event.target.className.indexOf('header-screen-hamburger') == -1;
      },
      openUrl(url) {
        window.open(url)
      },
      onClickOutside() {
        // console.log('Clicked outside (Using config), middleware returned true :)')
        this.$refs.languages.showLangMenu = false;
        this.showServicesMenu = false;
      },
      async login() {
        if (window.location.origin.indexOf('eternity.co.il') == -1) {
          await this.$store.dispatch('auth/loginTest');

          // setTimeout(() => {
          window.location.reload(true);
          // }, 200);
        } else {
          let url = decodeURIComponent(window.location.origin + '/auth/handle');
          location.href = 'https://eternity.co.il/sso?type=login&lang=' + this.$ml.current + '&redirectURL=' + url;
        }
      },
      handleScroll(event) {
        this.stickyHeader = window.scrollY > 100;
      },
    },
    beforeMount() {
      window.addEventListener('scroll', this.handleScroll);
    },
    beforeDestroy() {
      window.removeEventListener('scroll', this.handleScroll);
    },
  }
</script>